.tape__wrapper {
  padding: 111px 0 0;
}
.tape__title {
  font-family: SF Pro Display;
  font-weight: 600;
  font-size: 96px;
  line-height: 90%;
  letter-spacing: -5%;
  text-align: center;
  text-transform: uppercase;
  margin: 0 0 250px;
}
