  .tape__title {
    font-size: 56px;
    margin-bottom: 200px;
  }
}
@media screen and (max-width: 500px) {
  .tape__title {
    font-size: 32px;
    margin-bottom: 100px;
  }
}
.tape .tape__conw {
  width: 110vw;
  overflow: hidden;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -55vw;
  margin-right: -55vw;
}
.tape .tape__top {
  width: 100vw;
  height: auto;
  margin-left: calc(-50vw + 50%);
  position: relative;
}
.tape .tape__top img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.tape .tape__bottom {
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  height: 56px;
  margin-top: -35px;
  position: relative;
  z-index: 1;
}
.tape .tape__bottom img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.tape .tape__conw-content {
  display: inline-flex;
  gap: 0;
  white-space: nowrap;
  margin: 0;
  padding: 0;
  will-change: transform;
  margin-top: -20px;
  animation: marquee linear infinite;
}
.tape .tape__conw-content svg {
  width: 60px;
  height: 100px;
  margin-right: -40px;
}
.tape .tape__conw-content > * {
  margin: 0;
  flex-shrink: 0;
}
@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%); /* Изменено с -100% на -50% */
  }
}
.tape__slider {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -55vw;
  margin-right: -55vw;
  margin-bottom: -55px;
  overflow: visible;
}
.tape .tape__slider-content {
  display: flex;
  will-change: transform;
  position: relative;
  z-index: 1;
  backface-visibility: hidden;
  transform-style: preserve-3d;
}
.tape .swiper-slide {
  max-width: 500px;
  height: 300px;
  flex: 0 0 auto;
}
.tape .swiper-slide .tape-image {
  width: 100%;
  height: 100%;
  position: relative;
}
.tape .swiper-slide .tape-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.tape .swiper-slide .tape-image .tape-info {
  position: absolute;
  top: 70px;
  left: 24px;
  max-width: 300px;
